/*
* For styles only loaded in the editor
* These styles are actually inlined using webpack style-loader.  They are not loaded using WordPress enqueuing.
*/
@media (min-width: 768px) {
  .wp-admin .col-full {
    max-width: 66.4989378333em;
    margin-left: auto;
    margin-right: auto;
    padding: 0 2.617924em;
    box-sizing: content-box; }
  .wp-admin .col-full:after,
  .wp-admin .col-full:before {
    content: "";
    display: table; }
  .wp-admin .col-full:after {
    content: " ";
    display: block;
    clear: both; } }

@media (max-width: 66.49894em) {
  .wp-admin .col-full {
    margin-left: 2.617924em;
    margin-right: 2.617924em;
    padding: 0; } }

.wp-admin .editor-styles-wrapper .storefront-has-sidebar .wp-block {
  max-width: 100%;
  overflow: hidden; }

.wp-admin .wp-block img:hover,
.wp-admin .wp-block img:focus {
  cursor: copy; }
